@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;
}



html{
  scroll-behavior: smooth;
}

body{
  background-color: #111;
}

::-moz-selection {
  color: #fff;
  background: #f9a826b0;
}

::selection {
  color: #fff;
  background: #F9A826;
}